body{
	margin: auto;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
	color: #232528;
	font-family: sans-serif;
}

header{
	display: -ms-flexbox;
	display: flex;
	text-align: center;
	background-color: #540D6E;
}

h1{
	color: white;
	font-weight: bold;
	font-family: 'Handlee', cursive;
	display: -ms-flexbox;
	display: flex;
}

h2{
	font-family: 'Handlee', cursive;
	border-bottom: 2px solid #FFA400;
	color: #FFA400;
}

.h2Summary{
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
	-ms-flex-pack: start;
	    justify-content: flex-start;
	-ms-flex-align: baseline;
	    align-items: baseline;
}

.h2Summary div{
	width: 50%;
	text-align: left;
	color: #FFA400;
}
.btnDownload{
	width: 50%;
	text-align: right;
	color: #FFA400;
}
.btnDownload:hover{
	color: #540d6e;
}
main article{
	display: -ms-flexbox;
	display: flex;
}

section{
	display: -ms-flexbox;
	display: flex;
}

#lesson {
	text-align: left;
}
#summary img{
	margin: auto;
}
.videoList{
	list-style: none;
	border-bottom: 1px solid #009FFD;
	margin: 0;
	padding: 0;
}
.list-group-item{
	border: 1px solid #009FFD;
	border-bottom: none;
	background-color: white;
	color: #009FFD;
	display: -ms-flexbox;
	display: flex;
}
.list-group-item:hover{
	background-color: #009FFD;
	color: white;
	border-bottom: none;
	cursor: pointer;
}
.currentVideo{
	background-color: #009FFD;
	color: white;
	border: 1px solid #009FFD;
	display: -ms-flexbox;
	display: flex;
}

.nextQuiz{
	display: -ms-flexbox;
	display: flex;
}
.nextQuiz button, .validateBtn{
	background-color: #009FFD;
	color: white;
	font-family: 'Handlee', cursive;
	text-decoration: none;
	display: -ms-flexbox;
	display: flex;
	-webkit-appearance:none;
	-moz-appearance: none;
	border:none;
}
.nextQuiz button:hover, .validateBtn:hover{
	border:1px solid #009FFD;
	background-color: white;
	color: #009FFD;
	cursor: pointer;
	text-decoration: none;
}


/**********************************Style for the quiz page*****************************************/
#quizMain{
	display: -ms-flexbox;
	display: flex;
	padding:0;
}

.quiz{
	background-color: #FFA400;
	color: white;
	display: -ms-flexbox;
	display: flex;
}

.consigne{
	font-weight: bold;
}

form{
	display: -ms-flexbox;
	display: flex;
}

form input[type="text"], form input[type="number"]{
	outline: none;
	-moz-outline-color: transparent;
}
form input[type="text"]:focus, form input[type="number"]:focus{
	-webkit-box-shadow: 1px 1px 1px #FFA400;
	        box-shadow: 1px 1px 1px #FFA400;
	border: 1px solid #FFA400;
	-moz-outline-color: transparent;
}
form input[type="checkbox"],form input[type="radio"]{
	border: 1px solid #009FFD;
	-moz-outline-color: transparent;
}
.question select:focus{
	-webkit-box-shadow: 1px 1px 1px #FFA400;
	        box-shadow: 1px 1px 1px #FFA400;
	border: 1px solid #FFA400;
	outline: none;
	-moz-outline-color: transparent;
}
.percentage{
	display: -ms-flexbox;
	display: flex;
}

input[type="range"] {
	position: relative;
	color: #232528;
	-webkit-appearance: none;
	-moz-appearance: none;
	background-color: #FFA400;
	outline: none;
	-moz-outline-color: transparent;
	cursor: hand;
}

input[type="range"]:after,
input[type="range"]:before {
	position: absolute;
	color: #232528;
	-moz-outline-color: transparent;
}
input[type="range"]:before {
	left:0;
	content: attr(min);
}
input[type="range"]:after {
	right: 0;
	content: attr(max);
}

.disableBtn{
	cursor: not-allowed !important;
}
/********************************Score page***************************************************/
.scoreTitle{
	font-family: 'Handlee', cursive;
	color: #FFA400;
	text-align: center;
}

.boldTxt{
	font-weight: bold;
}
.resultNavig{
	display: -ms-flexbox;
	display: flex;
}
.resultNavig div{
	display: -ms-flexbox;
	display: flex;
}
.navigBtn{
	background-color: #009FFD;
	color: white;
	font-family: 'Handlee', cursive;
	text-decoration: none;
	display: -ms-flexbox;
	display: flex;
	-webkit-appearance:none;
	-moz-appearance: none;
	border:none;
}
.navigBtn:hover{
	border:1px solid #009FFD;
	background-color: white;
	color: #009FFD;
	cursor: pointer;
	text-decoration: none;
}
.resultConclusion{
	font-size: 1.5em;
	color: #540D6E;
	font-weight: bold;
}
.answerList{
	display: -ms-flexbox;
	display: flex;
}

.question div{
	display: -ms-flexbox;
	display: flex;
}
.question .fa-window-close{
	color: #FFA400;
}
.question .fa-check-square-o{
	color: #540D6E;
}
.listProcess{
	list-style: none;
}


