	}

	#lesson {
		-ms-flex-pack: center;
		    justify-content: center;
		-ms-flex-align: start;
		    align-items: flex-start;
	}

	.fa{
		margin-left: 20px;
	}

	#video{
		-ms-flex-pack: center;
		    justify-content: center;
		-ms-flex-align: center;
		    align-items: center;
		margin: 0;
		-ms-flex-direction: column;
		    flex-direction: column;
		width: 100%;

	}
	#video iframe{
		margin-bottom: 30px;
		margin-left: 0;
		width: 100%;
	}
	.video-detail{
		width: 100%;
	}
	.videoList{
		margin: 0;
		width: 100%;
