	}
	.list-group-item{
		-ms-flex:1 1 0%;
		    flex:1 1 0%;
		height: 100px;
		-ms-flex-direction: column;
		    flex-direction: column;
		-ms-flex-pack: center;
		    justify-content: center;
		-ms-flex-align: center;
		    align-items: center;
		margin: 0;
	}
	.currentVideo{
		-ms-flex:1 1 0%;
		    flex:1 1 0%;
		height: 99px;
		-ms-flex-direction: column;
		    flex-direction: column;
		-ms-flex-pack: center;
		    justify-content: center;
		-ms-flex-align: center;
		    align-items: center;
		margin-right: 0;
	}
	.list-group-item .fa, .currentVideo .fa{
		margin: 0;
	}
	.process{
		width: 90%;
	}
	.nextQuiz{
		-ms-flex-direction: row;
		    flex-direction: row;
		-ms-flex-pack: end;
		    justify-content: flex-end;
		width: 100%;
