		height: 40px;
		margin-top: 20px;
		margin-bottom: 50px;
		font-size: 1.5em;
		-ms-flex-pack: distribute;
		    justify-content: space-around;
		-ms-flex-align: center;
		    align-items: center;
	}


	/**********************************Style for the quiz page*****************************************/
	#quizMain{
		-ms-flex-pack: center;
		    justify-content: center;
		-ms-flex-align: center;
		    align-items: center;
		-ms-flex-direction: column;
		    flex-direction: column;
	}
	#quizTitle{
		-ms-flex-pack: start;
		    justify-content: flex-start;
		-ms-flex-align: center;
		    align-items: center;
	}
	.quiz{
		width: 90%;
		height: 50px;
		margin: 0;
		-ms-flex-pack: center;
