		height:32px;
	}
	form input[type="checkbox"],form input[type="radio"]{
		width: 20px;
		height: 20px;
	}
	.question select{
		width:200px;
		height:30px;
	}

	.percentage{
		-ms-flex-direction: row;
		    flex-direction: row;
		-ms-flex-pack: start;
		    justify-content: flex-start;
		-ms-flex-align: baseline;
