		font-size: 3em;
		width: 100%;
		margin-bottom: 0;
	}
	.resultQuiz{
		-ms-flex-pack: center;
		    justify-content: center;
		-ms-flex-align: center;
		    align-items: center;
		margin: 10px;
	}
	.navig{
		-ms-flex-direction: column;
		    flex-direction: column;
		-ms-flex-align: center;
		    align-items: center;
		-ms-flex-pack: center;
		    justify-content: center;
		margin-top: 70px;
		width: 90%;
	}
	.boldTxt{
		font-size: 1.5em;
	}
	.resultNavig{
		-ms-flex-pack: distribute;
		    justify-content: space-around;
		-ms-flex-direction: row;
		    flex-direction: row;
		-ms-flex-align: center;
		    align-items: center;
		width: 100%;
		-ms-flex-wrap: wrap;
		    flex-wrap: wrap;
	}
