		-ms-flex-pack: distribute;
		    justify-content: space-around;
		-ms-flex-direction: column;
		    flex-direction: column;
		-ms-flex-align: center;
		    align-items: center;
	}
	.navigBtn{
		width: 130px;
		height: 40px;
		margin-top: 10px;
		margin-bottom: 10px;
		font-size: 1.5em;
		-ms-flex-pack: distribute;
		    justify-content: space-around;
		-ms-flex-align: center;
		    align-items: center;
	}
	.resultConclusion{
		-ms-flex-pack: center;
		    justify-content: center;
		-ms-flex-align: center;
		    align-items: center;
		font-size: 1.5em;
		width: 90%;
	}
	.resultConclusion div{
		text-align: center;
	}
	.answerList{
		-ms-flex-pack: center;
